import { createArticle } from '@/api/article'
export default {
  name: 'editArticle',
  // 在路由渲染组件之前会先执行中间件处理
  middleware: 'authenticated',
  data() {
    return {
      formData: {
       "article": {
        "title": "",
        "description": "",
        "body": "",
        "tagList": []
        }
      }
    }
  },
  methods: {
    async onSubmit() {
      const { data } = await createArticle(this.formData)
      const article = data.article
      if ( article ) {
        alert('文章发布成功')
      }
    }
  }
}
